@import "../../scss/mixins.scss";
@import "../../scss/variables.scss";
.ButtonPrimory {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 20px 0;
	width: 100%;
	background: var(--linear-green);
	border: none;
	border-radius: 20px;
	box-shadow: 0px 4px 9px 0px rgba(0, 0, 0, 0.25);
	color: var(--button-text);
	font-family: GT Walsheim Pro;
	font-size: px-to-em(20);
	font-style: normal;
	font-weight: 500;
	line-height: 130%;
	cursor: pointer;

	@media (max-width: $mob) {
		font-size: px-to-em(16);
		padding: 15px 0;
		border-radius: 15px;
	}
}
